import { useState } from 'react';
import Head from 'next/head';
import { useRouter } from 'next/router';

// Dummy data for visualization
const INITIAL_ITEMS = [
    { id: 1, name: 'Item_name', price: 100.00, quantity: 1 },
    { id: 2, name: 'Item_name', price: 100.00, quantity: 1 },
    { id: 3, name: 'Item_name', price: 100.00, quantity: 1 },
    { id: 4, name: 'Item_name', price: 100.00, quantity: 1 },
    { id: 5, name: 'Item_name', price: 100.00, quantity: 1 },
];

export default function CartPro() {
    const router = useRouter();
    const [items, setItems] = useState(INITIAL_ITEMS);

    const updateQuantity = (id: number, delta: number) => {
        setItems(prev => prev.map(item => {
            if (item.id === id) {
                const newQuantity = Math.max(1, item.quantity + delta);
                return { ...item, quantity: newQuantity };
            }
            return item;
        }));
    };

    const removeItem = (id: number) => {
        setItems(prev => prev.filter(item => item.id !== id));
    };

    const total = items.reduce((sum, item) => sum + (item.price * item.quantity), 0);

    // We'll show the delete button for the 4th item to match the screenshot example
    // In a real app this would typically be a swipe action or hover state
    const showDeleteForId = 4;

    return (
        <>
            <Head>
                <title>Cart Pro - Community Cart</title>
            </Head>

            <div style={{
                maxWidth: '500px',
                margin: '0 auto',
                padding: '20px',
                minHeight: '100vh',
                display: 'flex',
                flexDirection: 'column',
                fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif'
            }}>

                {/* Header */}
                <header style={{
                    display: 'flex',
                    alignItems: 'center',
                    marginBottom: '30px',
                    position: 'relative',
                    padding: '10px 0'
                }}>
                    <button
                        onClick={() => router.back()}
                        style={{
                            background: 'none',
                            border: 'none',
                            fontSize: '24px',
                            cursor: 'pointer',
                            padding: '10px',
                            position: 'absolute',
                            left: -10
                        }}
                    >
                        ❮
                    </button>
                    <h1 style={{
                        fontSize: '18px',
                        fontWeight: '600',
                        margin: '0 auto',
                        textAlign: 'center'
                    }}>
                        Cart
                    </h1>
                </header>

                {/* List */}
                <div style={{ display: 'flex', flexDirection: 'column', gap: '20px', flex: 1 }}>
                    {items.map(item => (
                        <div key={item.id} style={{ display: 'flex', gap: '15px' }}>
                            <div style={{
                                flex: 1,
                                border: '1px solid #707070',
                                borderRadius: '15px',
                                padding: '15px',
                                display: 'flex',
                                gap: '15px',
                                alignItems: 'flex-start',
                                backgroundColor: 'white'
                            }}>
                                <div style={{
                                    width: '70px',
                                    height: '70px',
                                    backgroundColor: '#E0E0E0',
                                    borderRadius: '8px',
                                    flexShrink: 0
                                }} />

                                <div style={{ flex: 1, display: 'flex', flexDirection: 'column', justifyContent: 'space-between', height: '70px' }}>
                                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                                        <div>
                                            <div style={{ fontWeight: '600', fontSize: '14px', marginBottom: '4px' }}>{item.name}</div>
                                            <div style={{ fontSize: '12px', color: '#888', marginBottom: '4px' }}>Quantity</div>
                                            <div style={{ fontSize: '14px', color: '#4CAF50', fontWeight: '500' }}>₹{item.price.toFixed(2)}</div>
                                        </div>
                                    </div>
                                </div>

                                <div style={{
                                    display: 'flex',
                                    alignItems: 'center',
                                    gap: '15px',
                                    alignSelf: 'flex-end',
                                    marginBottom: '5px'
                                }}>
                                    <button
                                        onClick={() => updateQuantity(item.id, 1)}
                                        style={{
                                            background: 'none',
                                            border: 'none',
                                            color: '#4CAF50',
                                            fontSize: '20px',
                                            fontWeight: 'bold',
                                            cursor: 'pointer',
                                            padding: '0 5px'
                                        }}
                                    >
                                        +
                                    </button>
                                    <span style={{ fontWeight: '600', fontSize: '16px' }}>{item.quantity}</span>
                                    <button
                                        onClick={() => updateQuantity(item.id, -1)}
                                        style={{
                                            background: 'none',
                                            border: 'none',
                                            color: '#4CAF50',
                                            fontSize: '20px',
                                            fontWeight: 'bold',
                                            cursor: 'pointer',
                                            padding: '0 5px'
                                        }}
                                    >
                                        −
                                    </button>
                                </div>
                            </div>

                            {item.id === showDeleteForId && (
                                <button
                                    onClick={() => removeItem(item.id)}
                                    style={{
                                        backgroundColor: '#FF0000',
                                        border: 'none',
                                        borderRadius: '12px',
                                        width: '60px',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                        cursor: 'pointer',
                                        color: 'white',
                                        fontSize: '24px'
                                    }}
                                >
                                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                        <polyline points="3 6 5 6 21 6"></polyline>
                                        <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                                    </svg>
                                </button>
                            )}
                        </div>
                    ))}
                </div>

                {/* Footer */}
                <div style={{ marginTop: '30px' }}>
                    <div style={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        marginBottom: '20px',
                        padding: '0 10px'
                    }}>
                        <span style={{ fontSize: '20px', fontWeight: '500' }}>Total</span>
                        <span style={{ fontSize: '20px', fontWeight: '500' }}>₹{total.toFixed(1)}</span>
                    </div>

                    <button style={{
                        width: '100%',
                        padding: '18px',
                        backgroundColor: '#4CAF50',
                        color: 'white',
                        border: 'none',
                        borderRadius: '50px',
                        fontSize: '18px',
                        fontWeight: '600',
                        cursor: 'pointer',
                        boxShadow: '0 4px 6px rgba(76, 175, 80, 0.3)'
                    }}>
                        Checkout
                    </button>
                </div>
            </div>
        </>
    );
}
